@use "reset.css";
@use "_helpers.scss" as *;
@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Ubuntu&display=swap");
:root {
  --bg-color: #111013;
}
html,
body {
  height: 100%;
  background-color: var(--bg-color);

  box-sizing: border-box;
  font-family: "Roboto Mono", monospace;
  color: #fff;
}
#loading-screen {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  color: #111013;
  background-color: #fff;
  z-index: 9999;
}
.section {
  height: 100%;
  box-sizing: border-box;
}
.home {
  @include respond(fourK) {
    font-size: 2rem;
  }
  background: radial-gradient(
    50% 50% at 50% 50%,
    rgba(217, 217, 217, 0) 11.98%,
    rgba(217, 217, 217, 0.15) 97.92%
  );
  .grid_fondant {
    display: grid;

    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    .fondant {
      border-bottom-left-radius: 50px;
      border-bottom-right-radius: 100px;
      // background:rgba(216, 19, 19, 0.664)
    }
  }
  .content {
    @extend %centering;
    justify-content: space-around;
    flex-direction: column;
    height: 100%;
    position: relative;

    .logo_container {
      width: 90%;
      max-width: 400px;
      transform: translateX(-17px) !important;
      @include respond(fourK) {
        max-width: 800px;
      }
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 35px;
      //   transform: translateX(-10px);
    }
    .bakery_name_container {
      width: 80%;
      max-width: 300px;
      @include respond(fourK) {
        max-width: 500px;
      }
      transform: translateX(20px);
    }
    .choix_parfum {
      @extend %centering;
      flex-direction: column;
      gap: 15px;
      .choix_container {
        @extend %centering;
        gap: 15px;
        .parfum {
          cursor: pointer;
          width: 40px;
          transition: scale 1s ease;

          &:hover {
            transform: scale(1.1) !important;
          }
          > img {
            width: 100%;
            object-fit: contain;
          }
        }
      }
    }
  }
}

.presentation {
  font-family: "Ubuntu", sans-serif;
  @include respond(Smobile){
    height: fit-content;
  }
  @include respond(Tablet){
    height: 100%;
  }
  .content {
    display: flex;
    justify-content: space-between;
    padding: 50px 80px;
    @include respond(Smobile) {
      flex-direction: column-reverse;
    justify-content: center;
    gap: 60px;
    padding: 30px 25px;
    align-items: center;
    }
    @include respond(Tablet) {
      justify-content: space-between;
      gap: 0;
      flex-direction: row;
    }
    @include respond(Laptop) {
      padding: 50px 60px;
    }
    .text_container {
      @include respond(Tablet) {
        font-size: 1.2rem;
        width: 45%;
      }
      @include respond(Laptop) {
        font-size: 1.8rem;
      }
      @include respond(LLaptop) {
        font-size: 2.5rem;
      }

      @include respond(fourK) {
        font-size: 3.2rem;
      }
      font-size: 1.5rem;
      align-self: center;
      margin-top: 40px;
      margin-bottom: 20px;
      .text_line {
        position: relative;
      }
      .line_mask {
        z-index: 5;
        background-color: #111013;
        position: absolute;
        opacity: 0.5;
        width: 100%;
        height: 100%;
        top: 0;
        /* left: 0; */
        right: 0;
      }
    }
    .image_container {
      position: relative;
      z-index: 5;
      height: 45vh;
      min-height: 380px;
      @include respond(Smobile) {
        width: 90%;
      }
      @include respond(Tablet) {
        width: 40%;
        height: 75vh;
      }
      @include respond(Laptop) {
        width: 30%;
        height: 70vh;
      }
      .background_image {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        transform: translate(-123px, -37px) scale(1.1);
        z-index: -1;
        @include respond(Smobile) {
          transform: translate(-75px, 0px) scale(1.1);
        }
        @include respond(Laptop) {
          transform: translate(-123px, -37px) scale(1.1);
        }
        /* max-width: 500px; */
        /* max-height: 600px; */
      }
      .image {
        width: 100%;
        height: 100%;
        transform-origin: 100%;
        > img {
          max-width: 100%;
          max-height: 100%;
          object-fit: contain;
          border-radius: 10px;
          width: 100%;
          height: 100%;
          box-shadow: 0 5px 10px rgb(154 160 185 / 5%),
            0 15px 40px rgb(166 173 201 / 20%);
          object-fit: cover;
        }
        &.one {
          position: inherit;
        }
        &.two {
          position: absolute;
          top: 0;
          left: 0;
        }
        &.three {
          position: absolute;
          top: 0;
          left: 0;
        }
      }
    }
  }
}

.gateaux {

  @include respond(Smobile){
    margin-top: 20px;
  }
  & + .section {
    @include respond(Smobile) {
      display: none;
    }
  }
  .content {
    display: flex;
    width: 100%;
    height: 100%;
    @include respond(Smobile) {
      flex-direction: column;
    }
    @include respond(Laptop) {
      flex-direction: row;
    }
    .text_anim {
      display: flex;
      margin-left: 50%;
      font-size: 9em;
      @include respond(Smobile) {
        font-size: 2em;
        line-height: 3rem;
        margin-left: 0;
      }
      @include respond(Laptop) {
        font-size: 8em;
        line-height: inherit;
      }
      @include respond(fourK) {
        font-size: 18em;
      }
      font-weight: 600;
      text-transform: uppercase;
      overflow: hidden;
      .text_items {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        overflow: hidden;
        .text {
          display: flex;
          flex-wrap: nowrap;
          white-space: nowrap;
          > div {
            padding-left: 15px;
          }
        }
      }
    }
    .image_gateaux {
      @include respond(Smobile) {
        position: static;
        height: 70%;
      }
      @include respond(Laptop) {
        position: absolute;
        height: 100%;
      }
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 5;
      > img {
        width: 100%;
        object-fit: cover;
        max-width: 100%;
        height: 100%;
        max-height: 100%;
      }
    }
  }

  &.opposite {
    @include respond(Smobile){
      margin-top: 0px;
    }
    .content {
      @include respond(Smobile) {
        // flex-direction: column-reverse;
      }
    }
    .text_anim {
      margin-left: 0;
    }
    .image_gateaux {
      right: 0;
    }
  }
}
